<!-- 部门管理 -->
<template>
	<el-container style="padding: 10px;background:#fff;flex:1;display:flex;flex-direction:column;">
		<el-header height="32" class='current'>
			<h5 class="cur-title">部门车辆管理</h5>
		</el-header>
		<el-container style="padding: 0;background:#fff;flex:1;display:flex;">
			<el-main style="flex:1;padding:0;display:flex;">
				<div  class="left-box">
					<div class="title">部门架构</div>
					<div class="left-content">
						<div class="type-bar">
							<click-button-add class="button margin-top floatLeft" :listLi="listLi" :title="$t('divisionManage.leftTitle.addTitle')" :rules="rules" :formLabelWidth="formLabelWidth" :addDialog="addDialog" @addConfirm="addConfirm" @addInfoCancel="addCancel" @clickAddInfo="clickAddInfo"></click-button-add>
							<click-button-revise @reviseInfoCancel="reviseCancel" @reviseConfirm="reviseConfirm" @clickReviseInfo="clickRevisedpt" class="button margin-top floatLeft" :formLabelWidth="formLabelWidth" :listLi="listLi" :title="$t('menuManage.reviseTitle')" :rules="rules" :info="curNode" :reviseDialog="reviseDialog" :menuParentId="parentId"></click-button-revise>
							<el-button type="primary" @click="clickDelete" class="button margin-top floatLeft" size="small" icon="el-icon-close">{{$t('button.delete')}}</el-button>
							<span @click="clickReloadNode" class="el-icon-refresh"></span>
							<div class="search-bar">
								<el-input v-model="leftSearchV" class="search-input" size="small">
									<i slot="suffix" @click="leftSearch" class="el-input__icon search el-icon-search"></i>
								</el-input>
							</div>
						</div>
						<el-aside width="100%" style="flex:1;">
							<el-tree class="border-blue" :expand-on-click-node="false" empty-text default-expand-all :data="tree" @node-click="handleNodeClickFn" node-key="id" highlight-current ref="leftTree" :props="defaultProps">
								<span class="custom-tree-node" slot-scope="{ node, data }">
									<span v-if="data.child==undefined || data.child.length == 0" style="color:#00a8d4;font-size:14px;" class="icon iconfont">&#xe606;</span>
									<span v-if="data.child!=undefined && data.child.length != 0" style="color:#00a8d4;font-size:12px;" class="icon iconfont">&#xe602;</span>
									<span v-if="data.type=='node' && !data.opend" style="color:#00a8d4;font-size:14px;" class="icon iconfont">&#xe642;</span>
									<span :class="node.select ? 'active' : ''" style="font-size: 14px;">{{ node.label }}</span>
								</span>
							</el-tree>
						</el-aside>
					</div>
				</div>
				<div  class="center-box">
					<div class="title">已分配</div>
					<div class="center-content">
						<div class="type-bar">
							<el-checkbox class="left" v-model="wholeCarNumCenter">全选</el-checkbox>
							<div class="left">已选择：{{ centerCheckedTrueNum }}</div>
							<div class="search-bar">
								<el-input v-model="searchValueCenter" class="search-input" size="small">
									<i slot="suffix" class="el-input__icon search el-icon-search"></i>
								</el-input>
							</div>
						</div>
						<el-aside width="100%" style="flex:1;">
							<el-tree :expand-on-click-node="false" empty-text @check="changeChecked" :data="treeCenter" show-checkbox node-key="id" @node-click="handleNodeClickCenterFn" highlight-current default-expand-all ref="treeCenterDom" :props="defaultProps">
							</el-tree>
						</el-aside>
					</div>
				</div>
				<div class="move-info">
					<div class="move-btn">
						<span @click="clickMoveLeft" class="el-icon-d-arrow-left"></span>
						<span @click="clickMoveRight" class="el-icon-d-arrow-right"></span>
					</div>
				</div>
				<div  class="right-box">
					<h6 class="title">未分配</h6>
					<div class="right-content">
						<div class="type-bar">
							<el-checkbox class="left" @change="wholeCarNumRightFn" v-model="wholeCarNumRight">全选</el-checkbox>
							<div class="left">已选择：{{ rightCheckedTrueNum }}</div>
							<div class="search-bar">
								<el-input v-model="searchValueRight" class="search-input" size="small">
									<i slot="suffix" class="el-input__icon search el-icon-search"></i>
								</el-input>
							</div>
						</div>
						<el-aside width="100%" style="flex:1;padding: 10px 20px 0;">
							<div v-for="(item,index) in carNumList">
								<el-checkbox @change="changeCheckedRightFn(index)" v-model="item.checked">{{ item.text }}</el-checkbox>
							</div>
						</el-aside>
					</div>
				</div>
			</el-main>
		</el-container>
	</el-container>
</template>
<script>
import store from '@/store';
const clickButtonAdd = ()=> import("../../../components/clickButtonAdd");
const clickButtonRevise = ()=> import("../../../components/clickButtonRevise");
var ParentFn = ()=>import('../../../conmon/js/commo.js');
var Parent;
export default {
	name: 'divisionManage',
	components:{
		clickButtonAdd,
		clickButtonRevise
	},
	data(){
		return {
			curIndex: '0',
			tree: [],
			listLi: {},
			treeCenter: [],
			openList: [],
			defaultProps: {
				children: 'child',
				label: 'dptName'
			},
			carNumList: [],
			carId: '',
			leftSearchV: '',
			searchValueCenter: '',
			searchValueRight: '',
			wholeCarNumCenter: false,
			wholeCarNumRight: false,
			node: false,
			centerWholeCheckedNum: 0,
			centerCheckedTrueNum: 0,
			rightWholeCheckedNum: 0,
			rightCheckedTrueNum: 0,
			dialogTableVisible: false,
			formLabelWidth: '120px',
			title: '',
			curNode: {},
			addIsRevise: null,
			parentId: '00000000-0000-0000-0000-000000000000',
			rules: {},
			addDialog: {
				addDialog: false
			},
			reviseDialog: {
				reviseDialog: false
			}
		}
	},
	//组件已创建完毕，属性已绑定，dom还未生成
	created(){	
		
	},
	//模板挂载/编译之后
	mounted(){
		
	},
	activated(){
		this.setCreated();
	},
	methods: {
		//点击左侧发生改变
		setCreated(){
			var that = this;
			ParentFn().then(value =>{
				Parent = new value.default.ParentFn();
				this.rules = {
					dptName: [
					{validator: Parent.noIsEmpty('部门名称不能为空！'),required: true, trigger: 'blur' }
					],
					dptCode: [
					{validator: Parent.noIsEmpty('部门编码不能为空！'), required: true, trigger: 'blur' }
					]
				};
				Parent.error = this.$t('error');
				Parent.success = this.$t('success');
				Parent.button = this.$t('button');
				Parent.message = this.$t('message');
				Parent.divisionManage = this.$t('divisionManage');
				that.listLi = Parent.divisionManage.leftTitle.listLi;
				Parent.ajax(
					'api/services/app/SmDepartment/QueryAll',
					{
						id: that.parentId
					},
					that,
					(res)=>{
						if(res.success){
							var list = res.result.items;
							that.tree = Parent.setTreeChecked(list,'select');
							console.log(that.tree)
							that.curNode = list[0];
							that.$nextTick(()=>{
								that.$refs.leftTree.setCurrentKey(list[0].id);
							})
						}
					}
					)
			})
			//模拟请求的左边树
			var treeListLeft = [{
				id:'1',
				text: '深圳赤子物流有限公司',
				children: [{
					id:'2',
					text: '操作部'
				},
				{
					id:'3',
					text: '新车'
				}]
			}];
			// this.tree = treeListLeft;
			//模拟右边的车牌参数并给每一项添加checked属性
			var listRight = [{
				text:'粤B12345'
			},
			{
				text:'粤B52135'
			},
			{
				text:'粤B86524'
			},
			{
				text:'粤B13645'
			},
			{
				text:'粤B54623'
				// checked: false
			}];
			for(var i in listRight) {
				listRight[i].checked = false;
			}
			// this.carNumList = listRight;
		},
		//点击添加
		clickAddInfo(){
			this.addDialog.addDialog = true;
		},
		//点击添加确定
		addConfirm(res){
			Parent.testTreeName(this.tree,this.listLi.dptName.model,this.curNode.id);
			if(Parent.boole){
				this.$message.error(Parent.error.nameRepeat);
				return;
			}
			var obj = {};
			for(var key in res) {
				obj[key] = res[key].model;
			}
			obj.dptParentId = this.curNode.id;
			Parent.ajax(
				'api/services/app/SmDepartment/Create',
				obj,
				this,
				(res)=>{
					if(res.success) {
						this.$message({
							message: Parent.success.add,
							type: 'success'
						});
						this.addDialog.addDialog = false;
						for(var key in this.listLi){
							if(this.listLi[key].type == 'dropDown') {
								this.listLi[key].model = 0;
							} else {
								this.listLi[key].model = '';
							}
						}
						this.$refs.leftTree.append(res.result,obj.dptParentId);
					}
				}
				)
		},
		//点击添加取消
		addCancel(){
			this.addDialog.addDialog = false;
		},
		//点击修改部门
		clickRevisedpt() {
			if(this.curNode.id == this.parentId) {
				this.$message.error(Parent.error.menuRoot);
				return;
			};
			for(var key in this.listLi) {//选中修改的参数赋值给弹窗
				this.listLi[key].model = this.curNode[key];
			}
			this.reviseDialog.reviseDialog = true;
		},
		//点击修改确定
		reviseConfirm(listLi){
			//调用方法true证明有相同的menuName
			Parent.testReiveseTreeName(this.tree,this.listLi.dptName.model,this.curNode.id);
			if(Parent.boole){
				this.$message.error(Parent.error.nameRepeat);
				return;
			}
			var obj = {};
			var that = this;
			for(var key in listLi) {
				obj[key] = listLi[key].model;
			}
			obj.dptParentId = this.curNode.dptParentId;
			obj.id = this.curNode.id;
			Parent.ajax(
				'api/services/app/SmDepartment/Update',
				obj,
				this,
				(res)=>{
					if(res.success) {
						//使用递归方法重新更新树赋值
						var dptTree = JSON.parse(JSON.stringify(that.tree));
						that.tree = Parent.reviseMenuTree(dptTree,obj.id,res.result);
						that.reviseDialog.reviseDialog = false;
						that.curNode = res.result;
						that.$nextTick(()=>{
							that.$refs.leftTree.setCurrentKey(res.result.id);
						});
						that.$message({
							message: Parent.success.revise,
							type: "success"
						});
					}
				},
				'put'
				)
		},
		//点击修改取消
		reviseCancel(){
			this.reviseDialog.reviseDialog = false;
		},
		//点击删除
		clickDelete(){
			var curNodeId = this.curNode.id;
			if(this.parentId == curNodeId) {
				this.$message.error(Parent.error.menuRoot);
				return;
			}
			var that = this;
			Parent.nodeIsChild(this.tree,curNodeId);
			if(!Parent.boole) {
				this.$confirm(Parent.message.delete, Parent.message.title, {
					confirmButtonText: Parent.button.confirm,
					cancelButtonText: Parent.button.cancel,
					type: 'warning'
				}).then(() => {
					Parent.ajax(
						'api/services/app/SmDepartment/Delete?id='+curNodeId,
						'',
						that,
						(res)=>{
							if(res.success){
								that.$refs.leftTree.remove(curNodeId);
								that.$message({
									type: 'success',
									message: Parent.success.delete
								});
								//删除成功之后默认选中根节点
								that.curNode = that.tree[0];
								that.$refs.leftTree.setCurrentKey(that.curNode.id);
							}
						},
						'DELETE'
						)
				}).catch(() => {

				});
			} else {
				this.$message.error(Parent.error.menuNode);
			}
		},
		//根据搜索框的值来标记树的row
		leftSearch(){
			if(!this.leftSearchV) {
				return;
			}
			console.log(Parent.setTreeSelect(this.tree,'dptName','select',this.leftSearchV))
			// this.tree = Parent.setTreeSelect(this.tree,'dptName','select',this.leftSearchV);
		},
		//点击左边树
		handleNodeClickFn(data){
			//中间初始化选中的节点id
			this.carId = data.id;
			this.curNode = data;
			this.node = true;
			//发送请求获取不需要这样判断,暂时只是模拟
			/*if(data.children == undefined) {
				//证明没有子级
				this.treeCenter = [];
				this.treeCenter.push({text:data.text,children:data.children,id:data.id});
				setTimeout(()=>{
					this.$refs.treeCenterDom.setCurrentKey(data.id);
				},200)
			} else {
				this.treeCenter = [];
				//模拟中间的树形参数
				var treeListCenter = [{
					id:'1',
					text: '深圳赤子物流有限公司',
					children: [{
						id:'2',
						text: '操作部',
						children: [{
							id:'7',
							text: '粤C24523'
						},
						{
							id:'9',
							text: '粤C24873'
						}]
					},
					{
						id:'3',
						text: '新车',
						children: [{
							id:'10',
							text: '粤C24523'
						},
						{
							id:'11',
							text: '粤C24873'
						}]
					}]
				}];
				this.treeCenter = treeListCenter;
				setTimeout(()=>{
					this.$refs.treeCenterDom.setCurrentKey(data.id);
				},200)

				//中间树需要一个字段区分是否节点，否则就循环递归判断左边树的id或text值，等于就证明是节点
				var treeCenterNodeIdList = [];
				treeCenterNodeIdList = this.openListPushId(this.tree,treeCenterNodeIdList);
				this.setTreeChecked(this.treeCenter);
				for(var i in treeCenterNodeIdList) {
					this.signNode(treeCenterNodeIdList[i],this.treeCenter);
				}
				//将中间树数据所有的id push进this.openList，就可以将中间树打开
				this.openList = [];
				this.openList = this.openListPushId(this.treeCenter,this.openList);
				this.getCenterWholeNum(this.treeCenter);

			}*/
		},
		//递归push子集的元素
		getPushChild(list,pushList){
			for(var i in list) {
				if(list[i].children == undefined) {
					pushList.push({text: list[i].text,id:list[i].id})
				} else {
					pushList.push({text: list[i].text,id:list[i].id})
					pushList[i].children = [];
					this.getPushChild(list[i].children,pushList[i].children);		
				}
			}
			return pushList;
		},
		//根据id撤销中间的选中数据
		getPushChildId(list,id,refs) {
			for(var i in list) {
				if(list[i].id == id) {
					if(list[i].node) {
						this.getPushChildId(list[i].children,id,refs);
					} else {
						refs.remove(list[i].id);
					}
				} else {
					this.getPushChildId(list[i].children,id,refs);
				}
			}
		},
		//递归根据id判断需要将右边的数据放在哪一项下面
		getCurId(list,id,obj){
			for(var i in list) {
				if(list[i].id == id) {
					if(list[i].children == undefined){
						list[i].children = [];
					}
					list[i].children.push({text: obj.text});
				} else {
					if(list[i].children != undefined && list[i].children.length != 0) {
						this.getCurId(list[i].children,id,obj);
					}
				}
			}
		},
		//用左边树的id或者text值来判断中间树哪一个是节点并标记出来
		signNode(id,list){
			for(var i in list) {
				if(list[i].id == id) {
					list[i].node = true;
					return;
				} else {
					if(list[i].children != undefined && list[i].children.length > 0) {
						this.signNode(id,list[i].children);
					}
				}
			}
		},
		//递归改变中间树的复选框checked的参数
		changeCenterChecked(id,list,checked){
			for (var i = 0; i < list.length; i++) {
				if(list[i].id == id || id == '子级id'){
					list[i].checked = checked;
					if(list[i].node != undefined && list[i].children != undefined && list[i].children.length > 0) {
						// for(var j = 0;j < list[i].children.length;j++){
						// 	list[i].children[j].checked = !list[i].children[j].checked;
						// }

						this.changeCenterChecked('子级id',list[i].children,checked);
					}
				} else {
					if(list[i].node != undefined && list[i].children != undefined && list[i].children.length > 0) {
						this.changeCenterChecked(id,list[i].children,checked);
					}
				}
			}
		},
		//计算中间有多少选中的数量
		getCheckedNum(id,list){
			for(var i in list) {
				if(id == list[i].id || id == '全选') {
					if(list[i].node == undefined) {
						this.centerCheckedTrueNum++;
					}
					if(id != '全选') {
						return;
					} else {
						if(list[i].children != undefined && list[i].children.length > 0) {
							this.getCheckedNum(id,list[i].children);
						}
					}
				} else {
					if(list[i].children != undefined && list[i].children.length > 0) {
						this.getCheckedNum(id,list[i].children);
					}
				}
			}
		},
		//中间树全选的值之和
		getCenterWholeNum(list) {
			for(var i in list) {
				if(list[i].node) {
					if(list[i].children != undefined && list[i].children.length > 0) {
						this.getCenterWholeNum(list[i].children);
					}
				} else {
					this.centerWholeCheckedNum++;
				}
			}
		},
		//点击向左添加数据
		clickMoveLeft(){
			//是否是根节点
			if(this.node) {
				var boole = false;
				for(var i in this.carNumList){
					if(this.carNumList[i].checked){
						boole = true;
					}
				}
				if(!boole) {
					//证明没有至少选中一项
					this.$message({
						message: '请选择要添加的车辆！',
						type: 'warning'
					});
				} else {
					if(this.treeCenter.length > 0) {
						for(var i in this.carNumList){
							if(this.carNumList[i].checked){
								//需要动态获取数据，前端页面push没有id，会影响后续操作
								this.getCurId(this.treeCenter,this.carId,{text:this.carNumList[i].text});
								//将中间树数据所有的id push进this.openList，就可以将中间树打开
								this.openList = [];
								this.openList = this.openListPushId(this.treeCenter,this.openList);

							}
						}
					} 	else {
						this.$message({
							message: '请在左边树中选择部门！',
							type: 'warning'
						});
					}
				}
			} else {
				this.$message({
					message: '请在左边树中选择部门！',
					type: 'warning'
				});
			}
		},
		//点击向右撤销数据
		clickMoveRight(){
			if(this.treeCenter.length > 0) {
				if(this.centerCheckedTrueNum > 0) {
					var keyArr = this.$refs.treeCenterDom.getCheckedKeys();
					for(var i in keyArr) {
						this.getPushChildId(this.treeCenter,keyArr[i],this.$refs.treeCenterDom);
					}
					this.centerCheckedTrueNum = 0;
					this.centerWholeCheckedNum = 0;
					this.getCenterWholeNum(this.treeCenter);
					this.wholeCarNumCenter = false;
					this.$refs.treeCenterDom.setCheckedKeys([]);
				} else {
					this.$message({
						message: '请在中间树选择要撤销的车辆！',
						type: 'warning'
					});
				}
			} else {
				this.$message({
					message: '请在左边树中选择部门！',
					type: 'warning'
				});
			}
		},
		//选中右侧复选框
		changeCheckedRightFn(index){
			this.rightCheckedTrueNum = 0;
			for(var i in this.carNumList) {
				if(this.carNumList[i].checked) {
					this.rightCheckedTrueNum++;
				}
			}
		},
		//右侧全选
		wholeCarNumRightFn(){
			if(this.wholeCarNumRight) {
				for(var i in this.carNumList){
					this.carNumList[i].checked = true;
				}
			} else {
				for(var i in this.carNumList){
					this.carNumList[i].checked = false;
				}
			}
		},
		handleNodeClickCenterFn(data,node){
			this.carId = data.id;
			if(data.node) {
				this.node = data.node;
			} else {
				this.node = false;
			}
		},

		//中间树只要子级
		//中间复选框发生改变
		changeChecked(data,children){
			//获取选中节点的key
			// console.log(this.$refs.treeCenterDom.getCheckedKeys())
			//获取选中节点的node
			// console.log(this.$refs.treeCenterDom.getCheckedNodes())
			//通过key删除一个节点
			// this.$refs.treeCenterDom.remove('11');
			//通过key获取一个节点
			// var getData = this.$refs.treeCenterDom.getNode('3');insertAfter
			//通过key添加一个节点
			// this.$refs.treeCenterDom.append({text:'你好',id: '90'},'2');
			this.$refs.treeCenterDom.setCurrentKey('1');

			var keyList = this.$refs.treeCenterDom.getCheckedKeys();
			this.centerCheckedTrueNum = 0;
			for(var i in keyList) {
				this.getCheckedNum(keyList[i],this.treeCenter);
			}
		},
		//点击添加部门
		clickAddNode(){
			this.dialogTableVisible = true;
			this.title = "添加部门";
			this.addIsRevise = '1';
			for(var key in this.listLi){
				this.listLi[key] = '';
			}
		},
		//点击删除部门
		clickDeleteNode() {
			if(this.carId != '') {
				this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
					center: true
				}).then(() => {
					this.$refs.leftTree.remove(this.carId);
					this.$message({
						type: 'success',
						message: '删除成功!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			} else {
				this.$message({
					type: 'warning',
					message: '请选择要删除的部门！'
				});
			}
		},
		//点击刷新部门
		clickReloadNode() {
			
		}
	},
	store,
	watch: {
		wholeCarNumCenter() {
			//监听wholeCarNumCenter为true的时候为全选
			if(this.wholeCarNumCenter) {
				var list = [];
				for(var i in this.tree) {
					list.push(this.tree[i].id);
				}
				this.$refs.treeCenterDom.setCheckedKeys(list);
				this.centerCheckedTrueNum = 0;
				this.getCheckedNum('全选',this.treeCenter);
			} else {
				this.$refs.treeCenterDom.setCheckedKeys([]);
				this.centerCheckedTrueNum = 0;
			}
		},
		wholeCarNumRight() {
			this.rightCheckedTrueNum = 0;
			if(this.wholeCarNumRight) {
				for(var i in this.carNumList) {
					if(this.carNumList[i].checked) {
						this.rightCheckedTrueNum++;
					}
				}
			}
		},
		centerCheckedTrueNum(){
			if(this.centerCheckedTrueNum == this.centerWholeCheckedNum) {
				this.wholeCarNumCenter = true;
			} else {
				this.wholeCarNumCenter = false;
			}
		}
	}
}
</script>
<style scoped>
.current{border-bottom: 2px solid #00a8d4;padding: 0;}
.current .cur-title{padding: 0 15px;height: 32px;line-height: 32px;color: #fff;background: #00a8d4;display: inline-block;}
.title-bar{display: flex;padding: 0;}
.title{height: 30px;padding: 10px 0 0;line-height: 30px;text-align: center;font-size: 14px;font-weight:600;}
.left-box,.center-box,.right-box{height:100%;flex:1;display:flex;flex-direction:column;margin-right: 20px;}
.right-box{margin:  0 0 0 20px;}
.left-box .left-content,.center-box .center-content,.right-box .right-content{flex: 1;border: 1px solid #ddd;display:flex;flex-direction:column;height:100%;}
.move-info{display: flex;justify-content: center;align-items: center;padding: 40px 0 0;}
.move-info span{display: block;width: 40px;height: 28px;line-height: 28px;color: #fff;text-align: center;background: #00a8d4;font-size: 16px;margin: 10px auto;border-radius: 2px;cursor: pointer;}
.type-bar{overflow: hidden;height: 41px;border-bottom: 1px solid #ddd;}
.type-bar span{display: block;float: left;width: 32px;height: 32px;font-size: 20px;color: #00a8d4;line-height: 32px;text-align: center;cursor: pointer;margin: 4px 0 0 5px;box-sizing: border-box;border: 1px solid #fff;}
.type-bar span:hover{border: 1px solid #dedede;border-radius: 2px;}
.type-bar .search-bar{float: right;position: relative;top: 4px;right: 5px;}
.type-bar .search-bar .search-input{width: 160px;}
.type-bar .search-bar .search{cursor: pointer;font-size: 16px;}
.car-num-list{padding: 6px 20px 0;}
.type-bar .left{float: left;margin: 11px 0 0 10px;line-height: 19px;font-size: 14px;color: #00a8d4;}
.margin-top{margin-top: 4px !important;}
.active{color: #fff !important;background: #00a8d4 !important;}
</style>


